body {
  background: #181830 url('https://images.unsplash.com/photo-1511512578047-dfb367046420?auto=format&fit=crop&w=1500&q=80') no-repeat center center fixed;
  background-size: cover;
  color: #f0f0f0;
  font-family: 'Roboto', 'Inter', 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 900px;
  margin: 40px auto;
  background: rgba(15, 18, 30, 0.95);
  border-radius: 16px;
  box-shadow: 0 0 24px rgba(0,0,0,0.75);
  padding: 48px 32px;
  animation: fadeIn 1.3s;
}

@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.95);}
  to { opacity: 1; transform: scale(1);}
}

.hero {
  text-align: center;
  margin-bottom: 2em;
  padding-bottom: 1em;
  border-bottom: 2px solid #2b2e42;
}

.hero h1 {
  font-size: 2.8em;
  font-family: 'Orbitron', 'Inter', sans-serif;
  color: #00d8ff;
  letter-spacing: 2px;
}

.hero h2 {
  color: #fcff4b;
  font-size: 1.4em;
  font-weight: 600;
  margin: 0.5em 0 0.5em;
}

.hero .contact a {
  color: #00d8ff;
  margin: 0 10px;
  font-size: 1.5em;
  transition: color 0.2s;
}
.hero .contact a:hover {
  color: #fcff4b;
}

.hero .gamepad {
  color: #fcff4b;
  margin-bottom: 16px;
  animation: spin 2.3s linear infinite;
}
@keyframes spin {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

.card {
  background: rgba(24, 24, 48, 0.98);
  border-radius: 12px;
  margin-bottom: 2em;
  padding: 1.6em 2em;
  box-shadow: 0 2px 18px #1a1a2b55;
  border-left: 6px solid #00d8ff;
  transition: box-shadow 0.2s;
}
.card:hover {
  box-shadow: 0 0 32px #00d8ff55;
}

.card h3 {
  color: #fcff4b;
  font-size: 1.3em;
  font-family: 'Orbitron', 'Inter', sans-serif;
  margin-bottom: 0.7em;
}

ul {
  margin: 0.5em 0 0.5em 1em;
  padding: 0;
}

li {
  margin-bottom: 0.5em;
}

.timeline .job {
  margin-bottom: 2em;
}

.timeline .job h4 {
  color: #00d8ff;
  font-size: 1.15em;
  margin: 0.2em 0;
}
.timeline .job span {
  color: #fcff4b;
  font-size: 0.98em;
  margin-bottom: 0.6em;
  display: block;
}

.game-credit {
  color: #00d8ff;
  font-weight: bold;
  letter-spacing: 1px;
}

footer {
  text-align: center;
  margin: 2em 0 0 0;
  color: #888;
  font-size: 1em;
  letter-spacing: 1px;
}

@media (max-width: 600px) {
  .container {
    padding: 12px 4px;
  }
  .card {
    padding: 1em 0.5em;
  }
  .hero h1 {
    font-size: 2em;
  }
}